.slider {
  position: relative;
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 97px;
  text-align: center;

  @media (max-width: 1200px) {
      width: calc(100vw - 60px);
  }

  @media (max-width: 1200px) {
      width: calc(100vw - 90px);
  }

  @media (max-width: 1023px) {
      padding-top: 46px;
  }

  @media (max-width: 767px) {
      width: calc(100vw - 30px);
      padding-top: 42px;
  }
}

.slider h2 {
  margin: 0;
  padding: 35px 0;
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-weight: bold;
  font-size: 34px;

  @media (max-width: 767px) {
      padding: 30px 0;
  }
}

.slider__link {
  font-weight: 500;
  color: $black;
  border-bottom: 1px solid $brown;
  padding: 5px;

  &:hover {
      color: $brown;
  }

  &:active {
      opacity: 0.7;
  }
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 60px;
  margin-bottom: 42px;

  @media (max-width: 1023px) {
      padding-top: 50px;
      margin-bottom: 35px;
  }

  @media (max-width: 767px) {
      padding-top: 45px;
      margin-bottom: 30px;
  }
}

.slider__item a {
  display: block;
  height: 100%;
  color: $black;

  &:hover,
  &:focus {
      outline: none;
      box-shadow: 0 6px 12px $grey;
  }
}

.slider__item img {
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 22px;

  @media (max-width: 767px) {
      margin-bottom: 15px;
  }
}

.slider__item p {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 500;
}

.slider__item span {
  font-weight: bold;
  font-size: 14px;
}

.slider__pagination {
  display: flex;
  justify-content: center;

  .swiper-pagination-bullet {
      padding: 6px;
      font-weight: 500;
      color: $grey;
      cursor: pointer;

      &:hover {
          color: $brown;
      }

      &:active {
          opacity: 0.7;
      }
  }

  .swiper-pagination-bullet-active {
      color: $black;
      cursor: default;

      &:hover {
          color: $black;
      }

      &:active {
          opacity: 1;
      }
  }
}

.slider__buttons-container {
  position: absolute;
  width: 100%;
  top: 155px;
  display: flex;
  justify-content: space-between;

  @media (max-width: 1023px) {
      top: 112px;
  }

  @media (max-width: 767px) {
      display: none;
  }
}

.slider__button {
  padding: 5px 0;
  background-color: transparent;
  border: none;
  cursor: pointer;

  svg {
      stroke: $brown;

      @media (max-width: 1023px) {
          width: 36px;
          height: 15px;
      }
  }

  &:hover svg,
  &:focus svg {
      stroke: $black;
  }

  &:active {
      opacity: 0.7;
  }
}

.slider__button--prev {
  transform: rotate(180deg);
}

.swiper-button-disabled,
.swiper-button-disabled:hover {
  cursor: default;

  svg {
      stroke: $grey;
  }
}

.swiper-button-disabled:active {
  opacity: 1;
}
