html {
  scroll-behavior: smooth;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Cormorant Garamond"), url("../fonts/cormorantgaramond-regular.woff2") format("woff2"), url("../fonts/cormorantgaramond-regular.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond Italic";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: local("Cormorant Garamond Italic"), url("../fonts/cormorantgaramond-italic.woff2") format("woff2"), url("../fonts/cormorantgaramond-italic.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Cormorant Garamond Semi-bold"), url("../fonts/cormorantgaramond-semibold.woff2") format("woff2"), url("../fonts/cormorantgaramond-semibold.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Cormorant Garamond Bold"), url("../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Poppins"), url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Poppins Medium"), url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Poppins Bold"), url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
}
.body {
  min-height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #1D1613;
}

.header {
  position: relative;
}
.header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  top: 114px;
  background-color: #f6f5f4;
}
@media (max-width: 1023px) {
  .header::before {
    display: none;
  }
}

.header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 45px;
}
@media (max-width: 1023px) {
  .header__top {
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 50px;
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .header__top {
    margin-bottom: 35px;
    margin-top: 13px;
  }
}

@media (max-width: 1023px) {
  .header__top form {
    display: none;
    position: relative;
    order: 1;
    width: 100%;
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header__top form {
    padding-top: 24px;
  }
}

.header__top label {
  cursor: pointer;
}
.header__top label svg {
  fill: #1D1613;
  stroke: none;
}
@media (max-width: 1023px) {
  .header__top label {
    position: absolute;
    bottom: 8px;
    left: 12px;
  }
  .header__top label svg {
    width: 16px;
    height: 16px;
    fill: #FFFFFF;
  }
}

.header__top input {
  padding: 10px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1D1613;
  border: none;
  border-radius: 0;
  width: 175px;
}
.header__top input::placeholder {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9B9997;
}
.header__top input:focus {
  outline: none;
  border-bottom: 1px solid #A87B62;
}
@media (max-width: 1023px) {
  .header__top input {
    width: 100%;
    margin-left: 0;
    padding-left: 40px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 21px;
  }
  .header__top input::placeholder {
    visibility: hidden;
  }
  .header__top input:focus {
    border-bottom: none;
  }
}
@media (max-width: 767px) {
  .header__top input {
    margin-top: 0;
  }
}

.header__nav-toggle {
  display: none;
}
@media (max-width: 1023px) {
  .header__nav-toggle {
    display: block;
    width: 25px;
    height: 25px;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
}

.header__logo {
  position: absolute;
  top: 30px;
  transform: translateX(-33px);
}
@media (max-width: 1023px) {
  .header__logo {
    padding-right: 7px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    top: 20px;
    transform: translateX(-26px);
  }
}

@media (max-width: 767px) {
  .header__logo img {
    width: 52px;
    height: 52px;
  }
}

.header__user-nav {
  display: flex;
  flex-wrap: wrap;
  padding-top: 11px;
  max-width: 500px;
}
@media (max-width: 1023px) {
  .header__user-nav {
    padding-top: 0;
    width: auto;
  }
}

.header__link {
  font-weight: 500;
  font-size: 14px;
  color: #1D1613;
  margin-right: 35px;
}
.header__link:last-child {
  margin-right: 0;
}
.header__link:hover {
  color: #A87B62;
}
.header__link:active {
  opacity: 0.7;
}

@media (max-width: 1023px) {
  .header__link--login {
    display: none;
  }
}

@media (max-width: 1023px) {
  .header__link span {
    display: none;
  }
}

.header__link svg {
  display: none;
}
@media (max-width: 1023px) {
  .header__link svg {
    display: block;
  }
}

@media (max-width: 1023px) {
  .header__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 10px;
}
@media (max-width: 1023px) {
  .header__nav-list {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 0;
    padding-top: 15px;
    margin-bottom: 20px;
    box-sizing: border-box;
  }
}
@media (max-width: 767px) {
  .header__nav-list {
    margin-bottom: 10px;
  }
}

.header__nav-item {
  padding: 20px 0 20px;
  margin-right: 25px;
}
.header__nav-item:last-child {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .header__nav-item {
    padding: 17px 0;
  }
}

.header__nav-link {
  font-weight: 500;
  color: #1D1613;
  padding: 5px;
}
.header__nav-link:hover {
  color: #A87B62;
}
.header__nav-link:active {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .header__nav-link {
    font-size: 24px;
    color: #FFFFFF;
  }
  .header__nav-link:hover {
    color: #9B9997;
  }
  .header__nav-link:active {
    opacity: 0.7;
  }
}

.header__tablet-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 25px;
}
@media (max-width: 767px) {
  .header__tablet-list {
    margin-bottom: 15px;
  }
}
@media (min-width: 1023px) {
  .header__tablet-list {
    display: none;
  }
}

.header__tablet-item {
  padding: 10px 0;
}

.header__tablet-item a {
  font-weight: 500;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.75);
}
.header__tablet-item a:hover {
  color: #9B9997;
}
.header__tablet-item a:active {
  opacity: 0.5;
}

.header__tablet-login {
  margin-bottom: 65px;
  padding: 15px 0;
  font-weight: 500;
  font-size: 18px;
  color: #FFFFFF;
}
.header__tablet-login:hover {
  color: #9B9997;
}
.header__tablet-login:active {
  opacity: 0.7;
}
@media (min-width: 1023px) {
  .header__tablet-login {
    display: none;
  }
}

@media (max-width: 1023px) {
  .header__nav--closed .header__nav-list,
.header__nav--closed .header__tablet-list,
.header__nav--closed .header__tablet-login {
    display: none;
  }

  .header__wrapper--active form {
    display: block;
  }
}
@media (max-width: 1023px) {
  .header__wrapper--nojs {
    background-color: #A87B62;
  }
  .header__wrapper--nojs svg,
.header__wrapper--nojs .header__logo svg {
    stroke: #FFFFFF;
  }
  .header__wrapper--nojs .header__link:hover svg, .header__wrapper--nojs .header__link:focus svg {
    stroke: #1D1613;
  }
  .header__wrapper--nojs .header__nav-toggle {
    opacity: 0;
  }
  .header__wrapper--nojs .header__nav-list,
.header__wrapper--nojs .header__tablet-list,
.header__wrapper--nojs .header__tablet-login {
    display: flex;
  }
}

.header__wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 30px;
  text-align: center;
}
@media (max-width: 1200px) {
  .header__wrapper {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
  }
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 20px;
  }
}

.header__wrapper--active {
  height: 100vh;
  overflow-y: scroll;
  background-color: #A87B62;
}

.header__wrapper svg {
  stroke: #1D1613;
}

.header__wrapper--active svg,
.header__wrapper--active .header__logo svg {
  stroke: #fff;
}

.header__wrapper .header__nav-toggle:hover svg, .header__wrapper .header__nav-toggle:focus svg,
.header__wrapper .header__link:hover svg,
.header__wrapper .header__link:focus svg {
  stroke: #A87B62;
}
.header__wrapper .header__nav-toggle:active svg,
.header__wrapper .header__link:active svg {
  stroke: #9B9997;
}

.header__wrapper--active .header__nav-toggle:hover svg, .header__wrapper--active .header__nav-toggle:focus svg,
.header__wrapper--active .header__link:hover svg,
.header__wrapper--active .header__link:focus svg {
  stroke: #1D1613;
}

.promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 463px;
  padding: 0 35px;
  color: #FFFFFF;
  font-family: "Cormorant Garamond", "Georgia", serif;
  text-align: center;
  background-color: #A87B62;
  background-image: url("../img/promo-desktop.jpg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .promo {
    background-image: url("../img/promo-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-tablet@2x.jpg");
  }
}

.promo h2 {
  margin: 15px 0;
  font-weight: bold;
  font-size: 45px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}
@media (max-width: 1023px) {
  .promo h2 {
    padding-right: 18px;
  }
}
@media (max-width: 767px) {
  .promo h2 {
    font-size: 38px;
    margin: 15px 0 5px;
    padding-right: 0;
  }
}

.promo p {
  margin: 0;
  font-weight: 400;
  font-size: 38px;
  line-height: 49px;
  font-family: "Cormorant Garamond Italic", "Georgia", serif;
  margin-bottom: 0;
}
@media (max-width: 1023px) {
  .promo p {
    padding-right: 20px;
  }
}
@media (max-width: 767px) {
  .promo p {
    font-size: 34px;
    padding-right: 0;
  }
}

.promo--catalog {
  min-height: 261px;
  background-image: url("../img/promo-catalog-desktop.jpg");
}
@media (min-resolution: 2dppx) {
  .promo--catalog {
    background-image: url("../img/promo-catalog-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .promo--catalog {
    background-image: url("../img/promo-catalog-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .promo--catalog {
    background-image: url("../img/promo-catalog-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .promo--catalog {
    background-image: url("../img/promo-catalog-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .promo--catalog {
    background-image: url("../img/promo-catalog-mobile@2x.jpg");
  }
}

.promo--catalog h2 {
  font-size: 34px;
  letter-spacing: normal;
}
@media (max-width: 1023px) {
  .promo--catalog h2 {
    padding-right: 0;
    margin: 10px 0 14px;
  }
}

.promo--catalog p {
  font-weight: 500;
  font-size: 22px;
  line-height: 29px;
}
@media (max-width: 1023px) {
  .promo--catalog p {
    padding-right: 0;
  }
}

.slider {
  position: relative;
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 97px;
  text-align: center;
}
@media (max-width: 1200px) {
  .slider {
    width: calc(100vw - 60px);
  }
}
@media (max-width: 1200px) {
  .slider {
    width: calc(100vw - 90px);
  }
}
@media (max-width: 1023px) {
  .slider {
    padding-top: 46px;
  }
}
@media (max-width: 767px) {
  .slider {
    width: calc(100vw - 30px);
    padding-top: 42px;
  }
}

.slider h2 {
  margin: 0;
  padding: 35px 0;
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-weight: bold;
  font-size: 34px;
}
@media (max-width: 767px) {
  .slider h2 {
    padding: 30px 0;
  }
}

.slider__link {
  font-weight: 500;
  color: #1D1613;
  border-bottom: 1px solid #A87B62;
  padding: 5px;
}
.slider__link:hover {
  color: #A87B62;
}
.slider__link:active {
  opacity: 0.7;
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 60px;
  margin-bottom: 42px;
}
@media (max-width: 1023px) {
  .slider__list {
    padding-top: 50px;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .slider__list {
    padding-top: 45px;
    margin-bottom: 30px;
  }
}

.slider__item a {
  display: block;
  height: 100%;
  color: #1D1613;
}
.slider__item a:hover, .slider__item a:focus {
  outline: none;
  box-shadow: 0 6px 12px #9B9997;
}

.slider__item img {
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 22px;
}
@media (max-width: 767px) {
  .slider__item img {
    margin-bottom: 15px;
  }
}

.slider__item p {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 500;
}

.slider__item span {
  font-weight: bold;
  font-size: 14px;
}

.slider__pagination {
  display: flex;
  justify-content: center;
}
.slider__pagination .swiper-pagination-bullet {
  padding: 6px;
  font-weight: 500;
  color: #9B9997;
  cursor: pointer;
}
.slider__pagination .swiper-pagination-bullet:hover {
  color: #A87B62;
}
.slider__pagination .swiper-pagination-bullet:active {
  opacity: 0.7;
}
.slider__pagination .swiper-pagination-bullet-active {
  color: #1D1613;
  cursor: default;
}
.slider__pagination .swiper-pagination-bullet-active:hover {
  color: #1D1613;
}
.slider__pagination .swiper-pagination-bullet-active:active {
  opacity: 1;
}

.slider__buttons-container {
  position: absolute;
  width: 100%;
  top: 155px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .slider__buttons-container {
    top: 112px;
  }
}
@media (max-width: 767px) {
  .slider__buttons-container {
    display: none;
  }
}

.slider__button {
  padding: 5px 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.slider__button svg {
  stroke: #A87B62;
}
@media (max-width: 1023px) {
  .slider__button svg {
    width: 36px;
    height: 15px;
  }
}
.slider__button:hover svg, .slider__button:focus svg {
  stroke: #1D1613;
}
.slider__button:active {
  opacity: 0.7;
}

.slider__button--prev {
  transform: rotate(180deg);
}

.swiper-button-disabled,
.swiper-button-disabled:hover {
  cursor: default;
}
.swiper-button-disabled svg,
.swiper-button-disabled:hover svg {
  stroke: #9B9997;
}

.swiper-button-disabled:active {
  opacity: 1;
}

.catalog {
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  padding-top: 80px;
  padding-bottom: 75px;
}
@media (max-width: 1200px) {
  .catalog {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
  }
}
@media (max-width: 1023px) {
  .catalog {
    flex-direction: column;
    padding-top: 25px;
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding-top: 15px;
    padding-bottom: 65px;
    padding-left: 15px;
    padding-right: 15px;
  }
