}
@media (max-width: 767px) {
  .catalog__item {
    width: calc((100% - 30px) / 2);
    margin-bottom: 22px;
  }
  .catalog__item:nth-child(3n) {
    margin-right: 30px;
  }
  .catalog__item:nth-child(2n) {
    margin-right: 0;
  }
}

.catalog__item a {
  display: block;
  height: 100%;
  padding-bottom: 3px;
  color: #1D1613;
}
.catalog__item a:hover, .catalog__item a:focus {
  outline: none;
  box-shadow: 0 6px 12px #9B9997;
}

.catalog__item img {
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 22px;
}
@media (max-width: 767px) {
  .catalog__item img {
    min-height: 136px;
    margin-bottom: 15px;
  }
}

.catalog__item p {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 500;
}

.catalog__item span {
  font-weight: bold;
  font-size: 14px;
}

.catalog__pagination {
  display: flex;
  justify-content: space-between;
}

.catalog__pagination-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
