.filter__block {
  position: relative;
  margin: 0;
  padding: 10px 25px 9px;
  border: 1px solid #A87B62;
}
.filter__block:not(:first-child) {
  border-top: none;
}
.filter__block:last-of-type {
  margin-bottom: 35px;
}
@media (max-width: 1023px) {
  .filter__block:last-of-type {
    margin-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .filter__block:last-of-type {
    margin-bottom: 10px;
  }
}
.filter__block::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 8px;
  right: 25px;
  top: 30px;
  background-image: url("../img/faq-icon.svg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .filter__block::before {
    width: 23px;
    height: 12px;
    right: 42px;
    top: 42px;
  }
}
@media (max-width: 767px) {
  .filter__block::before {
    width: 20px;
    height: 10px;
    right: 20px;
    top: 32px;
  }
}
.filter__block--active::before {
  transform: rotate(180deg);
}
@media (max-width: 1023px) {
  .filter__block {
    padding: 19px 21px 24px;
    border: none;
    border-bottom: 1px solid #A87B62;
  }
}
@media (max-width: 767px) {
  .filter__block {
    padding: 9px 21px;
    border: none;
    border-bottom: 1px solid #A87B62;
  }
}

.filter h3 {
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 10px;
}
.filter h3:hover {
  color: #A87B62;
}
.filter h3:focus {
  outline: none;
  color: #A87B62;
  border-bottom: 1px solid #1D1613;
}
.filter h3:active {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .filter h3 {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .filter h3 {
    margin-bottom: 15px;
  }
}

.filter__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  padding: 5px 10px;
}
@media (max-width: 1023px) {
  .filter__list {
    padding: 7px 15px;
  }
}

.filter__item {
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  padding: 5px 0 5px 25px;
}
.filter__item:hover {
  color: #A87B62;
  outline: none;
}
.filter__item:active {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .filter__item {
    margin-bottom: 7px;
  }
}

.filter__item input {
  position: absolute;
  left: 7px;
  opacity: 0;
  cursor: pointer;
}

.filter__item label {
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
}
.filter__item label::before {
  position: absolute;
  content: "";
  left: -3px;
  top: 8px;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid #A87B62;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .filter__item label::before {
    width: 18px;
    height: 18px;
    left: -5px;
    top: 6px;
  }
}
@media (max-width: 1023px) {
  .filter__item label {
    font-size: 16px;
  }
}

.filter__item input:checked + label::before {
  background-color: #A87B62;
}

.filter__item input:focus + label::before {
  outline: 2px solid #1D1613;
}

.filter__form-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 68px;
  margin-top: 25px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
  border: 1px solid #A87B62;
  border-radius: none;
  background-color: #A87B62;
  cursor: pointer;
}
.filter__form-button:hover {
  color: #1D1613;
  background-color: transparent;
}
.filter__form-button:active {
  opacity: 0.7;
}
.filter__form-button--reset {
  color: #1D1613;
  background-color: transparent;
}
.filter__form-button--reset:hover {
  color: #FFFFFF;
  background-color: #A87B62;
}
@media (max-width: 1023px) {
  .filter__form-button:not(:first-child) {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .filter__form-button:not(:first-child) {
    margin-top: 20px;
  }
}

.filter__range {
  display: none;
}

.filter__range-control {
  position: relative;
  display: flex;
  align-items: center;
  height: 24px;
  text-align: center;
  padding-top: 10px;
}
@media (max-width: 1023px) {
  .filter__range-control {
    height: 30px;
  }
}

.filter__scale {
  width: 100%;
  height: 1px;
  background-color: #f0eae4;
}

.filter__bar {
  position: absolute;
  left: 60px;
  width: 90px;
  height: 1px;
  background-color: #A87B62;
}
@media (max-width: 1023px) {
  .filter__bar {
    width: 180px;
  }
}
@media (max-width: 767px) {
  .filter__bar {
    width: 62%;
    left: 40px;
  }
}

.filter__toggle {
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: #A87B62;
  border-radius: 50%;
  cursor: pointer;
}
.filter__toggle--min {
  left: 44px;
}
@media (max-width: 1023px) {
  .filter__toggle--min {
    left: 55px;
  }
}
@media (max-width: 767px) {
  .filter__toggle--min {
    left: 12%;
  }
}
.filter__toggle--max {
  left: 148px;
}
@media (max-width: 1023px) {
  .filter__toggle--max {
    left: 231px;
  }
}
@media (max-width: 767px) {
  .filter__toggle--max {
    left: 73%;
  }
}
@media (max-width: 1023px) {
  .filter__toggle {
    width: 18px;
    height: 18px;
    top: 7px;
  }
}

.filter__price-control {
  display: flex;
  justify-content: space-between;
  padding: 4px 11px 8px 37px;
}
@media (max-width: 1023px) {
  .filter__price-control {
    padding: 0 112px 20px 45px;
  }
}
@media (max-width: 767px) {
  .filter__price-control {
    padding: 0 20px 17px 18px;
  }
}

.filter__price-control label {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
}
@media (max-width: 1023px) {
  .filter__price-control label {
    font-size: 16px;
  }
}

.filter__price-control input {
  position: relative;
  width: 40px;
  border: none;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  -moz-appearance: textfield;
}
@media (max-width: 1023px) {
  .filter__price-control input {
    font-size: 16px;
  }
}
.filter__price-control input:hover, .filter__price-control input:focus {
  appearance: none;
  -moz-appearance: textfield;
}

.filter__block--active .filter__list,
.filter__block--nojs .filter__list,
.filter__block--active .filter__range,
.filter__block--nojs .filter__range {
