@media (max-width: 1023px) {
  .filter__open-button {
    display: block;
  }
}

.filter__close-button {
  position: fixed;
  top: 11px;
  left: calc(50% + 210px);
  width: 32px;
  height: 32px;
  border: 1px solid #000000;
  border-radius: 50%;
  background-color: #000000;
  z-index: 30;
}
.filter__close-button:hover, .filter__close-button:active {
  border: 1px solid #A87B62;
  background-color: #A87B62;
}
.filter__close-button:active {
  opacity: 0.7;
}
.filter__close-button svg {
  fill: #FFFFFF;
}
@media (max-width: 767px) {
  .filter__close-button {
    top: 60px;
  }
}
@media (max-width: 490px) {
  .filter__close-button {
    left: calc(100% - 47px);
  }
}